const Menu = ({ handler, currentMenu})=>{
  const _handler = handler || (()=>{});
  return(
    <section className="menu">
      <div onClick={()=>{_handler(0)}} className={(currentMenu===0)?"activeMenu":""}>GitKraken</div>
      <div onClick={()=>{ _handler(1) }} className={(currentMenu===1)?"activeMenu":""}>Timers</div>
      <div onClick={() => { _handler(2) }} className={(currentMenu===2)?"activeMenu":""}>About</div>
    </section>
  );
}

export default Menu;
