import {useState} from 'react';
const CustomToolbar = ( {addTimer} )=>{
  const [formData, setFormData] = useState({
    description:"",
    time:60
  });

  const _addTimer = () => {
    if(addTimer && true){
      addTimer(formData);
    } else {
      console.log("No Timer Handler Assigned");
    }
  }

  const onChange=({target})=>{
    const {name, value} = target;
    setFormData(
      {...formData, [name]:value}
    )
  }
  return (
    <section className="ToolBar">
      <div>
        <label>Timer Name</label>
        <input type="text" name="description" value={formData.description} onChange={onChange} ></input>
      </div>
      <div>
        <label>Timer Seconds</label>
        <input type="number" min="5" max="86400" step="1" value={formData.time} name="time" onChange={onChange}></input>
      </div>
      <div>
      <button onClick={_addTimer}>Add New Timer</button>
      </div>
    </section>
  );
}

export default CustomToolbar;
