import { useEffect, useState} from 'react';

import ReactMarkdown from 'react-markdown';
import gfm from 'remark-gfm';

import { getBoards, getBoardById, getCardsFromColumn, getCardsById, addCommentToCard, updateCommentToCard, PATAssigned} from './boardClient';
import Timer from '../timer/timer';
import { playNotif } from './audio';
import './board.css';
import SetupKraken from './krakenSetup';
/*
 <Timer timeLimit={15} size={300} label="Task"></Timer>
 */
export default ({show}) => {

  const [boards, setBoards] = useState([]);
  const [currentBoard, setCurrentBoard] = useState({});
  const [cards, setCards] = useState([]);
  const [currentCard, setCurrentCard] = useState({});
  const [currentColumn, setCurrentColumn] = useState("");
  const [{ startTime, objective }, setTrack] = useState({ startTime: null, objective:""});
  const [endTime, setEndTime] = useState(null);
  const [showSetup, setShowSetup] = useState(true);

  useEffect(()=>{
    (async ()=>{
      if (PATAssigned()) {
        setShowSetup(false);
      }
    })()}, []
  );

  useEffect(() => {
    (async () => {
      if (PATAssigned()) {
        let data = await getBoards();
        setBoards(data);
      }
    })()
  }, [showSetup]
  );
  const boardOnClick = async (e)=>{
    const {id, name} = e.target.dataset;
    const data = await getBoardById(id);
    setCurrentBoard(data);
    setCards([]);
  }

  const columnOnClick = async (e)=>{
    const { id, name } = e.target.dataset;
    var data = await getCardsFromColumn(currentBoard.id, id);
    setCurrentColumn(name);
    setCards(data);
    setCurrentCard({});
  }

  const onCardClick = async (e) => {
      const { id, name } = e.target.dataset;
      var data = await getCardsById(currentBoard.id, id);
      setCurrentCard(data);
    }

  const counterStart = ()=>{
    let objectivet = prompt("Set objective");
    setTrack({ startTime: new Date(), objective: objectivet });
  }
  const counterEnd = ()=>{
    console.log(startTime, objective);
    playNotif();
    setEndTime(new Date());
  }

  useEffect( ()=>{
    if(endTime){
      (async ()=>{
        let data = {}
        if (!(currentCard.ledger.id && true)){
          const textData = `## Pomodoro Task Ledger

| Date | Start | End | Comment |
| - | - | - | - |
| ${startTime.toLocaleDateString()} | ${startTime.toLocaleTimeString()} | ${endTime.toLocaleTimeString()} | ${objective} |`;
          data = await addCommentToCard(currentBoard.id, currentCard.id, textData);
        } else {
          const textData = `${currentCard.ledger.text}
| ${startTime.toLocaleDateString()} | ${startTime.toLocaleTimeString()} | ${endTime.toLocaleTimeString()} | ${objective} |`;
          data = await updateCommentToCard(currentBoard.id, currentCard.id, currentCard.ledger.id, textData);
        }
        setCurrentCard({...currentCard, ledger:{id:data.id, text:data.text}});
        setEndTime(null);
      })();
    }
  },[endTime]
  )
  let listItems=null;
  if (currentBoard.id && true){
    listItems = (currentBoard.columns || []).map(
      (o, i)=>{
        return (<li key={i} data-id={o.id} data-name={o.name} onClick={columnOnClick}>{o.name}</li>);
      }
    );
  } else {
    listItems = boards.map((o, i) => {
      return (<li key={i} data-id={o.id} data-name={o.name} onClick={boardOnClick}>{o.name}</li>);
    });
  }
  let cardItems = (cards||[]).map((o,i)=>{
    return (<li key={i} className="card" >
      <span className="cardHeader" data-id={o.id} data-name={o.name} onClick={onCardClick}>{o.name}</span>
      {(o.description && true) ?
        (<ReactMarkdown plugins={[[gfm, { singleTilde: false }]]} >
          {o.description.text||""}
          </ReactMarkdown>) : ""
      }
      </li>
    );
  });
  if(showSetup){
    return (
      <section className={show ? "dashboard" : "pageHide"}>
        <SetupKraken onPatSave={()=>{setShowSetup(false);}} show={show}></SetupKraken>
      </section>
    )
  }

  return (
    <section className={show ? "dashboard" : "pageHide"}>
      <div className="filter">
        {currentBoard.id && (<h1><span className="bckBoards" onClick={()=>{
          setCurrentBoard({});
          setCards([]);
          setCurrentCard({});
        }}>&lt;</span><span className="boardTitle">{currentBoard.name} Columns</span></h1>)}
        {!currentBoard.id && (<h1>Gitkraken Boards</h1>)}
        <ul>
          {listItems}
        </ul>
        {cardItems.length > 0 && (<div><h2><span className="boardTitle">Cards of {currentColumn}</span></h2><ul>{cardItems}</ul></div>)}
      </div>
      {currentCard.id && (
        <div className="currentCard">
          <Timer
            timeLimit={1500}
            size={250}
            label={currentCard.name}
            counterStart={counterStart}
            counterEnd={counterEnd}
          ></Timer>
          <div className="card_markdown">
            <ReactMarkdown plugins={[[gfm, { singleTilde: false }]]}>
              {currentCard.ledger.text}
            </ReactMarkdown>
          </div>
        </div>
        )
      }
    </section>
  );
}
