import { useState, useEffect } from "react";
import notif from '../../assets/notif.mp4';

const audio = new Audio(notif);
let playing = false;
audio.addEventListener("ended", ()=>{playing=false});
export const playNotif = ()=>{
  if(!playing){
    audio.play();
  }
}

