import {useState, useEffect} from 'react';
import * as workerTimers from 'worker-timers';
import './timer.css';

export const COLOR_CODE = {
  info: "green",
  warning: "yellow",
  alert: "red"
}

export default ({timeLimit, color, size, label, counterStart, counterEnd}) => {
  const _timeLimit = timeLimit && true ? timeLimit : 15;
  const _color = color && true ? color: COLOR_CODE.info;
  const _size = size || 250;
  const _counterStart = counterStart || function(){} ;
  const _counterEnd = counterEnd || function () { };
  let InterValId = null;
  const _label = label || "";

  const [timeLeft, setTimeLeft] = useState(_timeLimit);
  const [running, setRunning] = useState(false);
  const [fractionArc, setFractionArc] = useState(283);


  function tick(time, timeLimit, handler, timeOut, label) {
    InterValId = workerTimers.setTimeout(
      () => {
        time -= 1;
        const timeFractionUnit = (timeLimit - time) / (timeLimit ** 2);
        let timeFraction = ((time / timeLimit) - timeFractionUnit) * 283;
        timeFraction = timeFraction < 0 ? 0 : timeFraction;
        if (time > 0) {
          handler(time, timeFraction);
          tick(time, timeLimit, handler, timeOut);
        } else {
          handler(time, timeFraction);
          timeOut();
        }
      },
      1000
    )
  }

  function formatTimeLeft(time) {
    const minutes = Math.floor(time / 60);
    let seconds = time % 60;
    if (seconds < 10) {
      seconds = `0${seconds}`;
    }
    return `${minutes}:${seconds}`;
  }


  useEffect(() => {
    if (!!!InterValId && running) {
      _counterStart();
      tick(timeLeft, _timeLimit, (time, fraction) => { setTimeLeft(time); setFractionArc(fraction); }, () => {setRunning(false); _counterEnd();});
      return () => {
        workerTimers.clearTimeout(InterValId);
        InterValId = null;
      }
    }
  }, [running]);

  let circlePathColor = `${_color}`;


  const timerBox = {width:`${_size}px`,height:`${_size}px`};
  const timerContent = { width: `${_size}px`};
  const labelBox = {}; //  {transform: `rotate(-90deg) translateX(-60%) translateY(-${(_size * 0.5 + 24)}px)`};
  return (
    <section style={timerContent}>
      <div className="timer_label" style={labelBox}>
        <span>{_label}</span>
      </div>
      <div style={timerBox}>
        <div className="base-timer" onClick={(e) => { if (timeLeft == 0) { setTimeLeft(_timeLimit); setFractionArc(283);} setRunning(true);}}>
          <svg className="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
            <g className="base-timer__circle">
              <circle className="base-timer__path-elapsed"
                cx="50" cy="50" r="45"
                fill={timeLeft == 0 ? circlePathColor: "white"}
              />
              <path
                id="base-timer-path-remaining"
                strokeDasharray={`${fractionArc} 283`}
                className="base-timer__path-remaining"
                color={circlePathColor}
                d="
              M 50, 50
              m -45, 0
              a 45,45 0 1,0 90,0
              a 45,45 0 1,0 -90,0
            "
              ></path>
            </g>
          </svg>
          <span id="base-timer-label" className="base-timer__label">
            {timeLeft == 0 ?
              (<span className="time_done">Done<br/>Press to Restart<br/>{formatTimeLeft(_timeLimit)}</span>):
              formatTimeLeft(timeLeft)
            }
          </span>
        </div>
      </div>
    </section>
  )
}
