import axios from 'axios';

const apiCliente = axios.create();

let PATToken = localStorage.getItem("PATToken") || "";

apiCliente.defaults.headers.common['cache-control'] = "no-cache";
apiCliente.defaults.headers.post['Content-Type'] = "no-cache";
apiCliente.defaults.headers.put['Content-Type'] = "no-cache";
apiCliente.defaults.baseURL = process.env.REACT_APP_GK_API;

if (PATToken !== "") {
  apiCliente.defaults.headers.common["Authorization"] = `Bearer ${PATToken}`;
}

// apiCliente.defaults.headers.common["Authorization"] = `Bearer ${process.env.REACT_APP_GK_PAT}`;

export default apiCliente;

export const PATAssigned = ()=>{
  return PATToken !== "";
}

export const setPAT = (PATTokenD)=>{
  PATToken = PATTokenD;
  apiCliente.defaults.headers.common["Authorization"] = `Bearer ${PATTokenD}`;
}

export const getBoards = async ()=>{
  let data = await apiCliente.get("boards");
  return data.data;
}

export const getBoardById = async (id)=>{
  let data = await apiCliente.get(`boards/${id}?fields=columns&fields=labels&fields=name`);

  return data.data;
}

export const getCardsFromColumn = async (boardId, columnId) => {
  let data = await apiCliente.get(`boards/${boardId}/columns/${columnId}/cards?fields=description&fields=name&fields=due_date`);

  return data.data;
}

export const getCardsById = async (boardId, cardId) => {
  let data = await apiCliente.get(`boards/${boardId}/cards/${cardId}?fields=description&fields=name&fields=due_date`);
  let commentData = await apiCliente.get(`boards/${boardId}/cards/${cardId}/comments?`);
  let commentLedge = {};
  if (commentData.data.length > 0) {
    commentData.data.map((o) => {
      if (o.text.indexOf("Pomodoro Task Ledger") >= 0) {
        commentLedge = { id: o.id, text: o.text }
      }
    });
  }
  return { ...data.data, ledger: commentLedge };
}

export const addCommentToCard = async (boardId, cardId, comment) => {
  let data = await apiCliente.post(`boards/${boardId}/cards/${cardId}/comments`, {text:comment});
  //
  return data.data;
}

export const updateCommentToCard = async (boardId, cardId, commentId, comment) => {
  let data = await apiCliente.post(`boards/${boardId}/cards/${cardId}/comments/${commentId}`, { text: comment });
  return data.data;
}
