import {useState, useEffect} from 'react';
import CustomToolbar from './cutomToolBar';
import Timer from '../timer/timer';
import { playNotif } from '../boardSetup/audio';

import './customTimer.css';
const CustomTimer = ({show})=>{
    const [timers, setTimers] = useState([]);

    useEffect( ()=>{
      const storedTimers = localStorage.getItem("PMDCTimers");
      if(storedTimers && true){
        setTimers(JSON.parse(storedTimers));
      }
    }, []);

    const addTimer = (data)=>{
      const newTimers = [...timers, data];
      localStorage.setItem("PMDCTimers", JSON.stringify(newTimers));
      setTimers(newTimers);
    }

    const counterEnd = () => {
      playNotif();
    }
    const timerWidgets = timers.map(
      (o, i)=>{
        return (<section key={i} className="TimerItem">
          <Timer
            timeLimit={o.time}
            color="green"
            label={o.description}
            counterEnd={counterEnd}
          ></Timer>
        </section>);
      }
    );

    return (
      <section className={show ? "CustomTimer" :"pageHide"}>
        <CustomToolbar addTimer={addTimer} />
        <section className="TimersBoard">
          {timerWidgets}
        </section>
      </section>
    )
}

export default CustomTimer;
