import './App.css';
import Board from './components/boardSetup/board';
import CustomTimer from './components/customTimers/customTimer';
import About from './components/about/about';
import Menu from './components/menu';
import {useState} from 'react';
function App() {
  const [activeMenu, setActiveMenu] = useState(0);

  return (
    <div className="App">
      <Menu handler={setActiveMenu} currentMenu={activeMenu}></Menu>
      <Board show={activeMenu === 0}></Board>
      <CustomTimer show={activeMenu === 1}></CustomTimer>
      <About show={activeMenu === 2}></About>
    </div>
  );
}

export default App;
